'use client'
import React, { useEffect, useRef } from 'react'
import { TokenResponse } from './types'

declare global { interface Window { Autodesk: any } }

export interface ViewerProps {
  getToken: () => Promise<TokenResponse>
  onViewerReady: (viewer: any) => void
}

const Viewer: React.FC<ViewerProps> = ({ getToken, onViewerReady }) => {
  const viewerRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const loadViewerScript = (): Promise<void> => {
      return new Promise((resolve, reject) => {
        // Skip if already loaded
        if (window.Autodesk && window.Autodesk.Viewing) {
          resolve()
          return
        }
        const script = document.createElement('script')
        script.src = 'https://developer.api.autodesk.com/modelderivative/v2/viewers/7.*/viewer3D.js'
        script.onload = () => resolve()
        script.onerror = () => reject(new Error('Viewer script failed to load'))
        document.head.appendChild(script)
      })
    }

    const initialize = async () => {
      try {
        await loadViewerScript()
        if (!viewerRef.current) return
        window.Autodesk.Viewing.Initializer(
          { env: 'AutodeskProduction', getAccessToken: (cb: (t: string, e: number) => void) => {
                getToken().then(({ access_token, expires_in }) => cb(access_token, expires_in))
              }
          },
          () => {
            const config = { extensions: ['Autodesk.DocumentBrowser'] }
            const v = new window.Autodesk.Viewing.GuiViewer3D(viewerRef.current!, config)
            v.start()
            v.setTheme('light-theme')
            onViewerReady(v)
          }
        )
      } catch (err) {
        console.error(err)
      }
    }

    initialize()
  }, [getToken, onViewerReady])

  return <div ref={viewerRef} className="relative flex-1" />
}

export default Viewer